
// src/components/bingo/JoinGame.js
import React, { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Paper,
  Box,
  Typography,
  TextField,
  Button,
  Alert,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  CircularProgress,
} from '@mui/material';
import io from 'socket.io-client';
import { BingoUserDataContext } from '../../context/BingoUserDataContext';
import CasinoIcon from '@mui/icons-material/Casino';
import InfoIcon from '@mui/icons-material/Info';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';

const socket = io('https://bosscrowns-api-a228488a1e46.herokuapp.com');

// Custom theme colors (consistent with CreateGame and GameMaster)
const theme = {
  primary: '#B43361',
  secondary: '#F3D4D4',
  background: '#FFFFFF',
  text: '#000000',
};

const JoinGame = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    pin: '',
  });
  const [error, setError] = useState(null);
  const { setUser, setIsLoading, isLoading } = useContext(BingoUserDataContext);
  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: name === 'pin' ? value.toUpperCase() : value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null);
    setIsLoading(true);

    if (!formData.name || !formData.email || !formData.pin) {
      setError('Please fill in all fields');
      setIsLoading(false);
      return;
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.email)) {
      setError('Please enter a valid email address');
      setIsLoading(false);
      return;
    }

    try {
      const response = await fetch(`https://bosscrowns-api-a228488a1e46.herokuapp.com/bingo/${formData.pin}/join`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ name: formData.name, email: formData.email }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to join game');
      }

      const { playerId, card } = await response.json();

      setUser({
        name: formData.name,
        email: formData.email,
        playerId,
        card,
        pin: formData.pin,
      });

      socket.emit('join-game', formData.pin);
      navigate(`/bingo/player/${formData.pin}`, {
        state: { email: formData.email, name: formData.name },
      });
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Container
      maxWidth="md"
      sx={{
        py: { xs: 3, sm: 5 },
        bgcolor: theme.background,
      }}
    >
      <Paper
       
        sx={{
          p: { xs: 2, sm: 4 },
         border:'none',
          bgcolor: theme.background,
        }}
      >
        <Box display="flex" flexDirection="column" alignItems="center" textAlign="center">
          <CasinoIcon
            sx={{
              fontSize: { xs: 40, sm: 60 },
              color: theme.primary,
              mb: 2,
            }}
          />
          <Typography
            variant="h3"
            component="h1"
            sx={{
              color: theme.text,
              fontWeight: 'bold',
              mb: 2,
              fontSize: { xs: '1.8rem', sm: '2.5rem', md: '3rem' },
            }}
          >
            Welcome to Crown Bingo!
          </Typography>
          <Typography
            variant="h6"
            sx={{
              color: theme.text,
              mb: 4,
              maxWidth: 600,
              fontSize: { xs: '1rem', sm: '1.25rem', md: '1.5rem' },
            }}
          >
            Join a bingo game by entering your details and the game PIN provided by the Game Master.
            Get ready for some fun!
          </Typography>

          {/* Instructions Section */}
          <Paper
            elevation={2}
            sx={{
              p: { xs: 2, sm: 3 },
              mb: 4,
               bgcolor: theme.lightGreen,
              borderRadius: 2,
              width: '100%',
              maxWidth: 600,
            }}
          >
            <Typography
              variant="h6"
              sx={{
                color: theme.text,
                mb: 2,
                display: 'flex',
                alignItems: 'center',
                fontSize: { xs: '1rem', sm: '1.25rem' },
              }}
            >
              <InfoIcon
                sx={{
                  mr: 1,
                  color: theme.primary,
                  fontSize: { xs: '1.2rem', sm: '1.5rem' },
                }}
              />
              How to Join a Game
            </Typography>
            <List dense>
              <ListItem>
                <ListItemIcon>
                  <InfoIcon
                    sx={{
                      color: theme.primary,
                      fontSize: { xs: '1rem', sm: '1.2rem' },
                    }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Enter your name and email."
                  secondary="Provide your details to join the game."
                  primaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                  }}
                  secondaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.8rem', sm: '0.9rem' },
                  }}
                />
              </ListItem>
              <ListItem>
                <ListItemIcon>
                  <InfoIcon
                    sx={{
                      color: theme.primary,
                      fontSize: { xs: '1rem', sm: '1.2rem' },
                    }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Input the game PIN."
                  secondary="Use the PIN provided by the Game Master."
                  primaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                  }}
                  secondaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.8rem', sm: '0.9rem' },
                  }}
                />
              </ListItem>
              <ListItem>
                <ListItemIcon>
                  <InfoIcon
                    sx={{
                      color: theme.primary,
                      fontSize: { xs: '1rem', sm: '1.2rem' },
                    }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Click 'Join Game' to start playing."
                  secondary="You'll receive a bingo card and join the game session."
                  primaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                  }}
                  secondaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.8rem', sm: '0.9rem' },
                  }}
                />
              </ListItem>
            </List>
          </Paper>

          {/* Form Section */}
          <Box
            component="form"
            onSubmit={handleSubmit}
            sx={{ mt: 2, width: '100%', maxWidth: { xs: 400, sm: 600 } }}
          >
            <TextField
              fullWidth
              label="Name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              placeholder="Enter your name"
              margin="normal"
              required
              variant="outlined"
              autoFocus
              sx={{
                mb: 2,
                '& .MuiOutlinedInput-root': {
                  '&.Mui-focused fieldset': {
                    borderColor: theme.primary,
                  },
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                },
                '& .MuiInputLabel-root': {
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                  '&.Mui-focused': {
                    color: theme.primary,
                  },
                },
              }}
            />
            <TextField
              fullWidth
              label="Email"
              name="email"
              type="email"
              value={formData.email}
              onChange={handleChange}
              placeholder="Enter your email"
              margin="normal"
              required
              variant="outlined"
              sx={{
                mb: 2,
                '& .MuiOutlinedInput-root': {
                  '&.Mui-focused fieldset': {
                    borderColor: theme.primary,
                  },
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                },
                '& .MuiInputLabel-root': {
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                  '&.Mui-focused': {
                    color: theme.primary,
                  },
                },
              }}
            />
            <TextField
              fullWidth
              label="Game Code"
              name="pin"
              value={formData.pin}
              onChange={handleChange}
              placeholder="Enter game PIN (e.g., ABC123)"
              margin="normal"
              required
              variant="outlined"
              sx={{
                mb: 2,
                '& .MuiOutlinedInput-root': {
                  '&.Mui-focused fieldset': {
                    borderColor: theme.primary,
                  },
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                },
                '& .MuiInputLabel-root': {
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                  '&.Mui-focused': {
                    color: theme.primary,
                  },
                },
              }}
            />
            {error && (
              <Alert
                severity="error"
                sx={{
                  mt: 2,
                  borderRadius: 2,
                  fontSize: { xs: '0.8rem', sm: '0.9rem' },
                }}
              >
                {error}
              </Alert>
            )}
            <Button
              type="submit"
              variant="contained"
              fullWidth
              disabled={isLoading}
              startIcon={isLoading ? <CircularProgress size={20} sx={{ color: theme.background }} /> : <ArrowForwardIcon />}
              sx={{
                mt: 3,
                py: 1.5,
                bgcolor: theme.primary,
                '&:hover': { bgcolor: theme.secondary },
                borderRadius: 8,
                fontSize: { xs: '0.9rem', sm: '1.1rem' },
              }}
            >
              {isLoading ? 'Joining...' : 'Join Game'}
            </Button>
          </Box>
        </Box>
      </Paper>
    </Container>
  );
};

export default JoinGame;