// src/components/bingo/Player.js
import React, { useState, useEffect, useContext } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import io from 'socket.io-client';
import {
  Container,
  Box,
  Typography,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Chip,
} from '@mui/material';
import BingoCard from './BingoCard';
import { BingoUserDataContext } from '../../context/BingoUserDataContext';
import CasinoIcon from '@mui/icons-material/Casino';
import InfoIcon from '@mui/icons-material/Info';
import LockIcon from '@mui/icons-material/Lock';

const socket = io('https://bosscrowns-api-a228488a1e46.herokuapp.com');

// Custom theme colors (consistent with GameMaster)
const theme = {
  primary: '#B43361',
  secondary: '#F3D4D4',
  background: '#FFFFFF',
  text: '#000000',
  lightGreen: '#E8F5E9', // Light green for instructions background
};

const Player = () => {
  const { pin } = useParams();
  const { state } = useLocation();
  const { user: userData } = useContext(BingoUserDataContext);
  const [name, setName] = useState(userData?.name || state?.name || 'Anonymous');
  const [email] = useState(userData?.email || state?.email || '');
  const [card, setCard] = useState(userData?.card || []);
  const [calledNumbers, setCalledNumbers] = useState([]);
  const [autoMark, setAutoMark] = useState(false);
  const [currentNumber, setCurrentNumber] = useState(null); // Store the latest called number
  const [winner, setWinner] = useState(null); // Store winner's name and email
  const [hasShownWinnerAlert, setHasShownWinnerAlert] = useState(false); // Track if alert was shown
  const letters = ['C', 'R', 'O', 'W', 'N']; // Map column indices to letters

  useEffect(() => {
    if (!userData?.playerId || !userData?.card) {
      return (
        <Container
          maxWidth="md"
          sx={{
            py: { xs: 3, sm: 5 },
          }}
        >
          <Box
            sx={{
              p: { xs: 2, sm: 4 },
              borderRadius: 2,
              textAlign: 'center',
            }}
          >
            <Box display="flex" alignItems="center" justifyContent="center" sx={{ mb: { xs: 1, sm: 2 } }}>
              <LockIcon sx={{ color: theme.primary, fontSize: { xs: '1.5rem', sm: '2rem' }, mr: 1 }} />
              <Typography
                variant="h6"
                sx={{
                  color: theme.text,
                  fontSize: { xs: '1rem', sm: '1.25rem' },
                }}
              >
                Please join a game first.
              </Typography>
            </Box>
          </Box>
        </Container>
      );
    }

    socket.emit('join-game', pin);

    socket.on('game-state', ({ calledNumbers, autoMark: serverAutoMark }) => {
      setCalledNumbers(calledNumbers);
      setAutoMark(serverAutoMark);
      if (calledNumbers.length > 0) {
        setCurrentNumber(calledNumbers[calledNumbers.length - 1]);
      }
      if (card.length > 0 && serverAutoMark) {
        markPreviousNumbers(calledNumbers);
      }
    });

    socket.on('number-called', ({ number, letter }) => {
      const combo = `${letter}${number}`;
      setCalledNumbers((prev) => [...prev, combo]);
      setCurrentNumber(combo); // Update current number
      if (autoMark) {
        markNumberByValue(number, letter);
      }
    });

    socket.on('auto-mark-updated', (newAutoMark) => {
      setAutoMark(newAutoMark);
      if (newAutoMark && card.length > 0) {
        markPreviousNumbers(calledNumbers);
      }
    });

    socket.on('player-won', ({ playerName, playerEmail }) => {
      setWinner({ name: playerName, email: playerEmail });
      if (!hasShownWinnerAlert) {
        // alert(`${playerName} has won!`);
        setHasShownWinnerAlert(true);
      }
    });

    return () => {
      socket.off('game-state');
      socket.off('number-called');
      socket.off('auto-mark-updated');
      socket.off('player-won');
    };
  }, [pin, card, calledNumbers, autoMark, email, hasShownWinnerAlert]);

  const markPreviousNumbers = (numbers) => {
    const newCard = card.map((row) =>
      row.map((cell, colIdx) => {
        if (!cell.number) return cell;
        const expectedLetter = letters[colIdx]; // Get letter based on column
        const combo = `${expectedLetter}${cell.number}`;
        return {
          ...cell,
          marked: cell.marked || numbers.includes(combo),
        };
      })
    );
    setCard(newCard);
    checkForWin(newCard);
  };

  const markNumberByValue = (number, letter) => {
    const colIdx = letters.indexOf(letter); // Find column for the letter
    if (colIdx === -1) return; // Invalid letter
    const newCard = card.map((row) =>
      row.map((cell, cIdx) => ({
        ...cell,
        marked: cell.marked || (cell.number === number && cIdx === colIdx),
      }))
    );
    setCard(newCard);
    checkForWin(newCard);
  };

  const markNumber = (rowIdx, colIdx) => {
    if (autoMark) return;
    const cell = card[rowIdx][colIdx];
    if (!cell.number || cell.marked) return;

    const expectedLetter = letters[colIdx]; // Get letter based on column
    const combo = `${expectedLetter}${cell.number}`;
    if (!calledNumbers.includes(combo)) return;

    const newCard = [...card];
    newCard[rowIdx][colIdx].marked = true;
    setCard(newCard);
    checkForWin(newCard);
  };

  const checkForWin = (card) => {
    // Check rows
    for (let row = 0; row < 5; row++) {
      if (card[row].every((cell) => cell.marked)) {
        socket.emit('player-won', { pin, playerName: name, playerEmail: email });
        return true;
      }
    }
    // Check columns
    for (let col = 0; col < 5; col++) {
      if (card.every((row) => row[col].marked)) {
        socket.emit('player-won', { pin, playerName: name, playerEmail: email });
        return true;
      }
    }
    // Check diagonals
    if (card.every((row, idx) => row[idx].marked)) {
      socket.emit('player-won', { pin, playerName: name, playerEmail: email });
      return true;
    }
    if (card.every((row, idx) => row[4 - idx].marked)) {
      socket.emit('player-won', { pin, playerName: name, playerEmail: email });
      return true;
    }
    return false;
  };

  // Game Over Screen
  if (winner) {
  const isWinner = winner.name === name;
  return (
    <Container
      maxWidth="md"
      sx={{
        py: { xs: 3, sm: 5 },
      }}
    >
      <Box
        sx={{
          p: { xs: 2, sm: 4 },
          borderRadius: 2,
          bgcolor: theme.background,
          textAlign: 'center',
        }}
      >
        <CasinoIcon
          sx={{
            fontSize: { xs: 40, sm: 60 },
            color: theme.primary,
            mb: { xs: 1, sm: 2 },
          }}
        />
        <Typography
          variant="h3"
          sx={{
            color: theme.text,
            fontWeight: 'bold',
            mb: { xs: 1, sm: 2 },
            fontSize: { xs: '1.8rem', sm: '2.5rem', md: '3rem' },
          }}
        >
          Game Over
        </Typography>
        <Typography
          variant="h4"
          sx={{
            color: isWinner ? 'success.main' : 'error.main',
            fontWeight: 'bold',
            mb: { xs: 2, sm: 3 },
            fontSize: { xs: '1.5rem', sm: '2rem', md: '2.5rem' },
          }}
          aria-live="assertive"
        >
          {isWinner ? 'Congratulations! You Won!' : `Winner: ${winner.name}`}
        </Typography>
        <Typography
          variant="h6"
          sx={{
            color: theme.text,
            mb: { xs: 2, sm: 3 },
            fontSize: { xs: '1rem', sm: '1.25rem', md: '1.5rem' },
          }}
        >
          Thank you for playing Crown Bingo!
        </Typography>
        {/* Display the player's Bingo Card */}
        <Box sx={{ mt: { xs: 2, sm: 3 } }}>
          <Typography
            variant="h5"
            sx={{
              color: theme.text,
              fontSize: { xs: '1.2rem', sm: '1.5rem' },
              mb: 2,
            }}
          >
            Your Bingo Card
          </Typography>
          <BingoCard card={card} onMark={() => {}} sx={{ mb: { xs: 2, sm: 3 } }} />
        </Box>
      </Box>
    </Container>
  );
}

  return (
    <Container
      maxWidth="md"
      sx={{
        py: { xs: 3, sm: 5 },
      }}
    >
      <Box
        sx={{
          p: { xs: 2, sm: 4 },
          borderRadius: 2,
          bgcolor: theme.background,
        }}
      >
        <Box display="flex" flexDirection="column" alignItems="center" textAlign="center">
          <CasinoIcon
            sx={{
              fontSize: { xs: 40, sm: 60 },
              color: theme.primary,
              mb: { xs: 1, sm: 2 },
            }}
          />
          <Typography
            variant="h3"
            sx={{
              color: theme.text,
              fontWeight: 'bold',
              mb: { xs: 1, sm: 2 },
              fontSize: { xs: '1.8rem', sm: '2.5rem', md: '3rem' },
            }}
          >
            Player: {name}
          </Typography>
          {/* Current Number Display */}
          {currentNumber && (
            <Typography
              variant="h4"
              sx={{
                color: theme.primary,
                fontWeight: 'bold',
                mb: { xs: 2, sm: 3 },
                fontSize: { xs: '1.5rem', sm: '2rem', md: '2.5rem' },
              }}
              aria-live="assertive"
            >
              Current Number: {currentNumber}
            </Typography>
          )}
          <Typography
            variant="h6"
            sx={{
              color: theme.text,
              mb: { xs: 2, sm: 4 },
              fontSize: { xs: '1rem', sm: '1.25rem', md: '1.5rem' },
            }}
          >
            Auto-Mark: {autoMark ? 'Enabled' : 'Disabled'} (Controlled by Game Master)
          </Typography>

          {/* Instructions Section */}
          

          <BingoCard card={card} onMark={markNumber} sx={{ mb: { xs: 2, sm: 3 } }} />

          {/* Called Numbers Section */}
          <Box sx={{ width: '100%', maxWidth: { xs: 400, sm: 600 } }}>
            <Typography
              variant="h5"
              sx={{
                color: theme.text,
                fontSize: { xs: '1.2rem', sm: '1.5rem' },
                mb: 1,
              }}
            >
              Called Numbers ({calledNumbers.length}/75)
            </Typography>
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
              {calledNumbers.length === 0 ? (
                <Typography
                  sx={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                    fontStyle: 'italic',
                  }}
                >
                  No numbers called yet.
                </Typography>
              ) : (
                calledNumbers.map((number, index) => (
                  <Chip
                    key={index}
                    label={number}
                    sx={{
                      bgcolor: theme.primary,
                      color: theme.background,
                      fontSize: { xs: '0.8rem', sm: '0.9rem' },
                    }}
                  />
                ))
              )}
            </Box>
            
          </Box>
          <Box
            sx={{
              p: { xs: 2, sm: 3 },
              mt:{ xs: 3, sm: 4 },
              mb: { xs: 2, sm: 4 },
              bgcolor: theme.lightGreen,
              borderRadius: 2,
              width: '100%',
              maxWidth: { xs: 400, sm: 600 },
            }}
          >
            <Typography
              variant="h6"
              sx={{
                color: theme.text,
                mb: { xs: 1, sm: 2 },
                display: 'flex',
                alignItems: 'center',
                fontSize: { xs: '1rem', sm: '1.25rem' },
              }}
            >
              <InfoIcon
                sx={{
                  mr: 1,
                  color: theme.primary,
                  fontSize: { xs: '1.2rem', sm: '1.5rem' },
                }}
              />
              Player Instructions
            </Typography>
            <List dense>
              <ListItem>
                <ListItemIcon>
                  <InfoIcon
                    sx={{
                      color: theme.primary,
                      fontSize: { xs: '1rem', sm: '1.2rem' },
                    }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Mark numbers on your card."
                  secondary="Click a number on your card to mark it if it has been called and auto-mark is disabled."
                  primaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                  }}
                  secondaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.8rem', sm: '0.9rem' },
                  }}
                />
              </ListItem>
              <ListItem>
                <ListItemIcon>
                  <InfoIcon
                    sx={{
                      color: theme.primary,
                      fontSize: { xs: '1rem', sm: '1.2rem' },
                    }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Watch for called numbers."
                  secondary="The latest called number is shown above, and all called numbers are listed below."
                  primaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                  }}
                  secondaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.8rem', sm: '0.9rem' },
                  }}
                />
              </ListItem>
              <ListItem>
                <ListItemIcon>
                  <InfoIcon
                    sx={{
                      color: theme.primary,
                      fontSize: { xs: '1rem', sm: '1.2rem' },
                    }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Win with a full row, column, or diagonal."
                  secondary="The game ends when a player achieves bingo."
                  primaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                  }}
                  secondaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.8rem', sm: '0.9rem' },
                  }}
                />
              </ListItem>
            </List>
          </Box>
        </Box>
        
      </Box>
    </Container>
  );
};

export default Player;