// src/components/bingo/GameMaster.js
import React, { useState, useEffect, useContext } from 'react';
import { useParams } from 'react-router-dom';
import io from 'socket.io-client';
import {
  Container,
  Box,
  Typography,
  TextField,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Button,
  FormControlLabel,
  Switch,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  CircularProgress,
  Chip,
} from '@mui/material';
import { UserDataContext } from '../../context/UserDataContext';
import CasinoIcon from '@mui/icons-material/Casino';
import InfoIcon from '@mui/icons-material/Info';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import StopIcon from '@mui/icons-material/Stop';
import LockIcon from '@mui/icons-material/Lock';

const socket = io('https://bosscrowns-api-a228488a1e46.herokuapp.com');

// Custom theme colors (consistent with JoinGame and CreateGame)
const theme = {
  primary: '#B43361',
  secondary: '#F3D4D4',
  background: '#FFFFFF',
  text: '#000000',
  lightGreen: '#E8F5E9', // Light green for instructions background
};

const GameMaster = () => {
  const { pin } = useParams();
  const { userData } = useContext(UserDataContext);
  const [game, setGame] = useState({ players: [], calledNumbers: [], autoMark: false });
  const [number, setNumber] = useState('');
  const [letter, setLetter] = useState('C');
  const [isAutoCalling, setIsAutoCalling] = useState(false);
  const [autoMark, setAutoMark] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [winner, setWinner] = useState(null); // State to store winner's name and email
  const [hasShownWinnerAlert, setHasShownWinnerAlert] = useState(false); // Track if alert was shown
  const [availableNumbers, setAvailableNumbers] = useState({
    C: Array.from({ length: 15 }, (_, i) => i + 1),
    R: Array.from({ length: 15 }, (_, i) => i + 16),
    O: Array.from({ length: 15 }, (_, i) => i + 31),
    W: Array.from({ length: 15 }, (_, i) => i + 46),
    N: Array.from({ length: 15 }, (_, i) => i + 61),
  });

  const ranges = {
    C: [1, 15],
    R: [16, 30],
    O: [31, 45],
    W: [46, 60],
    N: [61, 75],
  };

  const isAdmin = true; // Replace with actual auth logic

  useEffect(() => {
    if (!isAdmin) return;

    socket.emit('join-game', pin);

    socket.on('player-joined', (players) => {
      setGame((prev) => ({ ...prev, players }));
    });

    socket.on('player-won', ({ playerName, playerEmail }) => {
      setWinner({ name: playerName, email: playerEmail });
      if (!hasShownWinnerAlert) {
        alert(`${playerName} (${playerEmail}) has won!`);
        setHasShownWinnerAlert(true);
      }
      setIsAutoCalling(false);
    });

    const fetchGame = async () => {
      try {
        const response = await fetch(`https://bosscrowns-api-a228488a1e46.herokuapp.com/bingo/${pin}`);
        if (!response.ok) throw new Error('Game not found');
        const data = await response.json();
        setGame(data);
        setAutoMark(data.autoMark || false);
        setAvailableNumbers((prev) => {
          const newNumbers = { ...prev };
          data.calledNumbers.forEach((combo) => {
            const letter = combo[0];
            const num = parseInt(combo.slice(1));
            newNumbers[letter] = newNumbers[letter].filter((n) => n !== num);
          });
          return newNumbers;
        });
      } catch (err) {
        alert(err.message);
      }
    };
    fetchGame();

    return () => {
      socket.off('player-joined');
      socket.off('player-won');
    };
  }, [pin, isAdmin, hasShownWinnerAlert]);

  const callRandomNumber = () => {
    const letters = ['C', 'R', 'O', 'W', 'N'];
    const availableLetters = letters.filter((ltr) => availableNumbers[ltr].length > 0);
    if (availableLetters.length === 0) {
      setIsAutoCalling(false);
      alert('No more numbers to call!');
      return;
    }

    const selectedLetter = availableLetters[Math.floor(Math.random() * availableLetters.length)];
    const numbers = availableNumbers[selectedLetter];
    const randomIndex = Math.floor(Math.random() * numbers.length);
    const selectedNumber = numbers[randomIndex];

    socket.emit('call-number', { pin, number: selectedNumber, letter: selectedLetter });

    setGame((prev) => ({
      ...prev,
      calledNumbers: [...prev.calledNumbers, `${selectedLetter}${selectedNumber}`],
    }));

    setAvailableNumbers((prev) => ({
      ...prev,
      [selectedLetter]: prev[selectedLetter].filter((num) => num !== selectedNumber),
    }));
  };

  const toggleAutoMark = () => {
    if (!isAdmin) {
      alert('Only admins can control auto-marking');
      return;
    }
    const newAutoMark = !autoMark;
    setAutoMark(newAutoMark);
    socket.emit('set-auto-mark', { pin, autoMark: newAutoMark });
  };

  const toggleAutoCall = () => {
    if (!isAdmin) {
      alert('Only admins can control auto-calling');
      return;
    }
    setIsAutoCalling((prev) => !prev);
  };

  const callNumber = async () => {
    if (!isAdmin) {
      alert('Only admins can call numbers');
      return;
    }
    const num = Number(number);
    const [min, max] = ranges[letter];
    if (!number || num < min || num > max || availableNumbers[letter].indexOf(num) === -1) {
      alert(`Invalid number. Please enter a number between ${min} and ${max} that hasn't been called.`);
      return;
    }
    setIsLoading(true);
    try {
      socket.emit('call-number', { pin, number: num, letter });
      setGame((prev) => ({
        ...prev,
        calledNumbers: [...prev.calledNumbers, `${letter}${number}`],
      }));
      setAvailableNumbers((prev) => ({
        ...prev,
        [letter]: prev[letter].filter((n) => n !== num),
      }));
      setNumber('');
    } catch (err) {
      alert('Failed to call number');
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    let interval;
    if (isAutoCalling) {
      interval = setInterval(() => {
        callRandomNumber();
      }, 2000);
    }
    return () => clearInterval(interval);
  }, [isAutoCalling, availableNumbers]);

  if (!isAdmin) {
    return (
      <Container
        maxWidth="md"
        sx={{
          py: { xs: 3, sm: 5 },
          background: `linear-gradient(135deg, ${theme.secondary} 0%, ${theme.background} 100%)`,
        }}
      >
        <Box
          sx={{
            p: { xs: 2, sm: 4 },
            borderRadius: 2,
          
            textAlign: 'center',
          }}
        >
          <Box display="flex" alignItems="center" justifyContent="center" sx={{ mb: { xs: 1, sm: 2 } }}>
            <LockIcon sx={{ color: theme.primary, fontSize: { xs: '1.5rem', sm: '2rem' }, mr: 1 }} />
            <Typography
              variant="h6"
              sx={{
                color: theme.text,
                fontSize: { xs: '1rem', sm: '1.25rem' },
              }}
            >
              Access Denied: Only admins can access this page.
            </Typography>
          </Box>
        </Box>
      </Container>
    );
  }

  return (
    <Container
      maxWidth="md"
      sx={{
        py: { xs: 3, sm: 5 },
      }}
    >
      <Box
        sx={{
          p: { xs: 2, sm: 4 },
          borderRadius: 2,
          bgcolor: theme.background,
        }}
      >
        <Box display="flex" flexDirection="column" alignItems="center" textAlign="center">
          <CasinoIcon
            sx={{
              fontSize: { xs: 40, sm: 60 },
              color: theme.primary,
              mb: { xs: 1, sm: 2 },
            }}
          />
          <Typography
            variant="h3"
            sx={{
              color: theme.text,
              fontWeight: 'bold',
              mb: { xs: 1, sm: 2 },
              fontSize: { xs: '1.8rem', sm: '2.5rem', md: '3rem' },
            }}
          >
            Game Master Dashboard
          </Typography>
          <Typography
            variant="h6"
            sx={{
              color: theme.text,
              mb: { xs: 2, sm: 4 },
              fontSize: { xs: '1rem', sm: '1.25rem', md: '1.5rem' },
            }}
          >
            Manage your bingo game (PIN: {pin}). Call numbers, monitor players, and control the game flow.
          </Typography>

          {/* Instructions Section */}
          

          {/* Winner Display */}
          {winner && (
             <>
            <Typography
              variant="h4"
              sx={{
                color: 'green',
                fontWeight: 'bold',
                mb: { xs: 2, sm: 3 },
                fontSize: { xs: '1.5rem', sm: '2rem', md: '2.5rem' },
                textAlign: 'center',
              }}
              aria-live="assertive"
            >
              Winner: 
            </Typography>
              <Typography
              variant="h4"
              sx={{
                color: 'green',
                fontWeight: 'bold',
                mb: { xs: 2, sm: 3 },
                fontSize: { xs: '1.5rem', sm: '2rem', md: '2.5rem' },
                textAlign: 'center',
              }}
              aria-live="assertive"
            >
      {winner.name} ({winner.email})!
            </Typography>
            </>
          )}

          {/* Controls Section */}
          <Box
            sx={{
              mt: 2,
              mb: { xs: 2, sm: 3 },
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              gap: { xs: 2, sm: 2 },
              width: '100%',
              maxWidth: { xs: 400, sm: 600 },
            }}
          >
            <FormControl
              fullWidth
              sx={{
                maxWidth: { xs: 400, sm: 600 },
              }}
            >
              <InputLabel
                sx={{
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                  '&.Mui-focused': { color: theme.primary },
                }}
              >
                Letter
              </InputLabel>
              <Select
                value={letter}
                onChange={(e) => setLetter(e.target.value)}
                disabled={isAutoCalling || isLoading || winner}
                sx={{
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                  '& .MuiOutlinedInput-notchedOutline': {
                    borderColor: theme.text,
                  },
                  '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                    borderColor: theme.primary,
                  },
                }}
              >
                {['C', 'R', 'O', 'W', 'N'].map((ltr) => (
                  <MenuItem key={ltr} value={ltr} sx={{ fontSize: { xs: '0.9rem', sm: '1rem' } }}>
                    {ltr}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <TextField
              fullWidth
              type="number"
              value={number}
              onChange={(e) => setNumber(e.target.value)}
              placeholder={`Enter number (${ranges[letter][0]}-${ranges[letter][1]})`}
              disabled={isAutoCalling || isLoading || winner}
              aria-label="Number to call"
              sx={{
                maxWidth: { xs: 400, sm: 600 },
                '& .MuiOutlinedInput-root': {
                  '&.Mui-focused fieldset': {
                    borderColor: theme.primary,
                  },
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                },
                '& .MuiInputLabel-root': {
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                  '&.Mui-focused': { color: theme.primary },
                },
              }}
            />
            <Button
              onClick={callNumber}
              variant="contained"
              disabled={isAutoCalling || isLoading || winner}
              startIcon={isLoading ? <CircularProgress size={20} sx={{ color: theme.background }} /> : <CasinoIcon />}
              fullWidth
              sx={{
                bgcolor: theme.primary,
                '&:hover': { bgcolor: theme.secondary },
                padding: { xs: '8px 16px', sm: '10px 20px' },
                fontSize: { xs: '0.9rem', sm: '1rem' },
                borderRadius: 8,
                maxWidth: { xs: 400, sm: 600 },
              }}
            >
              Call Number
            </Button>
            <Button
              onClick={toggleAutoCall}
              variant="contained"
              disabled={isLoading || winner}
              startIcon={isAutoCalling ? <StopIcon /> : <PlayArrowIcon />}
              fullWidth
              sx={{
                bgcolor: isAutoCalling ? 'error.main' : 'success.main',
                '&:hover': {
                  bgcolor: isAutoCalling ? 'error.dark' : 'success.dark',
                },
                padding: { xs: '8px 16px', sm: '10px 20px' },
                fontSize: { xs: '0.9rem', sm: '1rem' },
                borderRadius: 8,
                maxWidth: { xs: 400, sm: 600 },
              }}
            >
              {isAutoCalling ? 'Stop Auto-Call' : 'Start Auto-Call'}
            </Button>
            <FormControlLabel
              control={
                <Switch
                  checked={autoMark}
                  onChange={toggleAutoMark}
                  color="primary"
                  disabled={isLoading || winner}
                />
              }
              label="Auto-Mark for All Players"
              sx={{
                maxWidth: { xs: 400, sm: 600 },
                '& .MuiFormControlLabel-label': {
                  fontSize: { xs: '0.9rem', sm: '1rem' },
                },
              }}
            />
          </Box>

          {/* Players Section */}
          <Box sx={{ width: '100%', maxWidth: { xs: 400, sm: 600 }, mb: { xs: 2, sm: 3 } }}>
            <Typography
              variant="h5"
              sx={{
                color: theme.text,
                fontSize: { xs: '1.2rem', sm: '1.5rem' },
                mb: 1,
              }}
            >
              Players ({game.players.length})
            </Typography>
            <List dense>
              {game.players.length === 0 ? (
                <Typography
                  sx={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                    fontStyle: 'italic',
                  }}
                >
                  No players have joined yet.
                </Typography>
              ) : (
                game.players.map((player) => (
                  <ListItem key={player._id}>
                    <ListItemText
                      primary={`${player.name} (${player.email})`}
                      primaryTypographyProps={{
                        color: theme.text,
                        fontSize: { xs: '0.9rem', sm: '1rem' },
                      }}
                    />
                  </ListItem>
                ))
              )}
            </List>
          </Box>

          {/* Called Numbers Section */}
          <Box sx={{ width: '100%', maxWidth: { xs: 400, sm: 600 } }}>
            <Typography
              variant="h5"
              sx={{
                color: theme.text,
                fontSize: { xs: '1.2rem', sm: '1.5rem' },
                mb: 1,
              }}
            >
              Called Numbers ({game.calledNumbers.length}/75)
            </Typography>
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
              {game.calledNumbers.length === 0 ? (
                <Typography
                  sx={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                    fontStyle: 'italic',
                  }}
                >
                  No numbers called yet.
                </Typography>
              ) : (
                game.calledNumbers.map((number, index) => (
                  <Chip
                    key={index}
                    label={number}
                    sx={{
                      bgcolor: theme.primary,
                      color: theme.background,
                      fontSize: { xs: '0.8rem', sm: '0.9rem' },
                    }}
                  />
                ))
              )}
            </Box>
            <Box
            sx={{
              p: { xs: 2, sm: 3 },
              mb: { xs: 2, sm: 4 },
              mt: { xs: 3, sm: 4 },
              bgcolor: theme.lightGreen,
              borderRadius: 2,
              width: '100%',
              maxWidth: { xs: 400, sm: 600 },
            }}
          >
            <Typography
              variant="h6"
              sx={{
                color: theme.text,
                mb: { xs: 1, sm: 2 },
                display: 'flex',
                alignItems: 'center',
                fontSize: { xs: '1rem', sm: '1.25rem' },
              }}
            >
              <InfoIcon
                sx={{
                  mr: 1,
                  color: theme.primary,
                  fontSize: { xs: '1.2rem', sm: '1.5rem' },
                }}
              />
              Game Master Instructions
            </Typography>
            <List dense>
              <ListItem>
                <ListItemIcon>
                  <InfoIcon
                    sx={{
                      color: theme.primary,
                      fontSize: { xs: '1rem', sm: '1.2rem' },
                    }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Call numbers manually or use auto-call."
                  secondary="Select a letter and number or start auto-calling every 2 seconds."
                  primaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                  }}
                  secondaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.8rem', sm: '0.9rem' },
                  }}
                />
              </ListItem>
              <ListItem>
                <ListItemIcon>
                  <InfoIcon
                    sx={{
                      color: theme.primary,
                      fontSize: { xs: '1rem', sm: '1.2rem' },
                    }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Enable auto-marking."
                  secondary="Toggle auto-mark to automatically mark players' cards."
                  primaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                  }}
                  secondaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.8rem', sm: '0.9rem' },
                  }}
                />
              </ListItem>
              <ListItem>
                <ListItemIcon>
                  <InfoIcon
                    sx={{
                      color: theme.primary,
                      fontSize: { xs: '1rem', sm: '1.2rem' },
                    }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Monitor players and called numbers."
                  secondary="View the list of players and track called numbers below."
                  primaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.9rem', sm: '1rem' },
                  }}
                  secondaryTypographyProps={{
                    color: theme.text,
                    fontSize: { xs: '0.8rem', sm: '0.9rem' },
                  }}
                />
              </ListItem>
            </List>
          </Box>
          </Box>
        </Box>
      </Box>
    </Container>
  );
};

export default GameMaster;